import { confirmAlert } from "react-confirm-alert";

export const confirmAlertDelete = (id, array, setState) =>
  confirmAlert({
    title: " ",
    message: "    ?",
    buttons: [
      {
        label: "",
        onClick: () => {
          const updateFiles = array.filter((item) => item.rnId !== id);
          setState(updateFiles);
        },
      },
      {
        label: "",
      },
    ],
  });
